import React, { useEffect, useState } from 'react';
import { useStaticQuery, graphql } from 'gatsby';

import styles from './index.module.scss';
import Switch from '../switch';

export default function Layout({ children }) {
    const data = useStaticQuery(graphql`
        query SiteTitleQuery {
            site {
                siteMetadata {
                    title
                }
            }
        }
    `);

    const [darkMode, setDarkMode] = useState(
        +(
            typeof window !== `undefined` &&
            window?.matchMedia('(prefers-color-scheme: dark)').matches
        )
    );
    const [audioSw, setAudioSw] = useState(1);
    const theme = darkMode ? styles.dark : styles.light;

    const toggleSw = (func, val, key) => {
        func(val);
        localStorage.setItem(`sw:${key}`, val);
    };

    useEffect(() => {
        if (localStorage.getItem('sw:dark') !== null) {
            setDarkMode(+localStorage.getItem('sw:dark'));
        }
        if (localStorage.getItem('sw:audio') !== null) {
            setAudioSw(+localStorage.getItem('sw:audio'));
        }
    }, []);

    return (
        <div className={`${theme} ${styles.wrapper}`}>
            <main className={styles.main}>{children}</main>
            <footer className={styles.footer}>
                <div>
                    © {new Date().getFullYear()},{' '}
                    {data?.site?.siteMetadata?.title || 'noCAPTCHA'}. i am not a
                    robot.
                </div>
                <div>
                    <Switch
                        checked={!darkMode}
                        onCheck={() =>
                            toggleSw(setDarkMode, +!darkMode, 'dark')
                        }
                    >
                        <span role="img" aria-label="lightSwitch">
                            💡
                        </span>
                    </Switch>
                    <Switch
                        checked={audioSw}
                        onCheck={() => toggleSw(setAudioSw, +!audioSw, 'audio')}
                    >
                        {audioSw ? (
                            <span role="img" aria-label="soundOn">
                                🔊
                            </span>
                        ) : (
                            <span role="img" aria-label="soundOff">
                                🔇
                            </span>
                        )}
                    </Switch>
                </div>
                <ul className={styles.links}>
                    <li>
                        <a href="https://www.instagram.com/nocaptcha.live/">
                            ig
                        </a>
                    </li>
                    <li>
                        <a href="/#/twitter">twitter</a>
                    </li>
                    <li>
                        <a href="/#/about">about</a>
                    </li>
                </ul>
            </footer>
        </div>
    );
}
